import React, { useCallback, useEffect, useState } from 'react';
import { SideBar, SideBarFooter, SideBarHeader, SideMenuContent, FilterText, SideMenuInput, SideMenuButton } from '../StyledComponents';
import { useNewsContext } from '../context';
import BrightnessHighIcon from '@material-ui/icons/BrightnessHigh';
import KeyboardIcon from '@material-ui/icons/Keyboard';
import Brightness4Icon from '@material-ui/icons/Brightness4';
import SearchIcon from '@material-ui/icons/Search';
import CloseIcon from '@material-ui/icons/Close';
import { Tooltip } from '@material-ui/core';

const SideMenu = () => {
    const { isSideBarOpen, setIsSideBarOpen, theme, setTheme, onSearchClick } = useNewsContext();
    const onThemeChangeHandle = (e) => {
        e.stopPropagation();
        setTheme(theme.mode === "dark" ? { mode: "light" } : { mode: "dark" })
    }
    const [keyword, setKeyword] = useState("");
    const [domains, setDomains] = useState("");

    const onEnterKeyDown = useCallback(event => {
        if (isSideBarOpen) {
            if (event.keyCode === 13) {
                onSearchClick(keyword, domains)
            }
        }
    }, [domains, isSideBarOpen, keyword, onSearchClick])

    useEffect(() => {

        window.addEventListener('keydown', onEnterKeyDown)
        return () => window.removeEventListener('keydown', onEnterKeyDown)

    }, [onEnterKeyDown])


    return <SideBar open={isSideBarOpen} >
        <SideBarHeader open={isSideBarOpen}>
            {isSideBarOpen ? <CloseIcon onClick={() => setIsSideBarOpen(!isSideBarOpen)} /> : <SearchIcon onClick={() => setIsSideBarOpen(!isSideBarOpen)} />}
        </SideBarHeader>
        <SideMenuContent open={isSideBarOpen}>
            <SideMenuInput theme={theme}>
                <FilterText>Keywords</FilterText>
                <input
                    type="text"
                    placeholder="Keywords or a phrase to search for."
                    value={keyword}
                    onChange={(e) => setKeyword(e.target.value)}
                ></input>
            </SideMenuInput>
            <SideMenuInput theme={theme}>
                <FilterText>Domains</FilterText>
                <input
                    type="text"
                    value={domains}
                    placeholder="bbc.co.uk, techcrunch.co.uk"
                    onChange={(e) => setDomains(e.target.value)}
                ></input>
            </SideMenuInput>
            <SideMenuButton theme={theme} onClick={() => onSearchClick(keyword, domains)}>
                Search
            </SideMenuButton>
        </SideMenuContent>
        <SideBarFooter open={isSideBarOpen}>
            {/* <Tooltip title="Use the Up and Down Arrow keys to navigate between articles" aria-label="keyboard" placement="left"><KeyboardIcon /></Tooltip> */}
            {theme.mode === "dark" ? <BrightnessHighIcon onClick={(e) => onThemeChangeHandle(e)} /> : <Brightness4Icon onClick={(e) => onThemeChangeHandle(e)} />}
        </SideBarFooter>
    </SideBar>
}

export default SideMenu;