import React from 'react';
import { useNewsContext } from '../context';
import { NoResultsMessage, NoResultsWrapper, AnchorUnderlined } from '../StyledComponents';

const NoResults = () => {

    const { setIsSideBarOpen, isSideBarOpen } = useNewsContext();
    return (
        <NoResultsWrapper>
            <NoResultsMessage>0 articles found.</NoResultsMessage>
            <AnchorUnderlined href=".">
                Top Headlines
            </AnchorUnderlined>
            <AnchorUnderlined onClick={() => setIsSideBarOpen(!isSideBarOpen)}>
                Refine Your Search
            </AnchorUnderlined>
        </NoResultsWrapper>
    )
}

export default NoResults;