
import React, { useEffect, useState } from 'react';
import GlobalStyle, { Body, CategoryFilters, Category, Container, PageTitle } from './StyledComponents';
import { ThemeProvider } from "styled-components";
import SideMenu from './components/SideMenu';
import { useNewsContext } from './context';
import Article from './components/Article';
import './App.css';
import Error from './components/Error';

function App() {

  const { theme, fetchError, onNavigateArticle, pageTitle } = useNewsContext();

  const [categories, setCategories] = useState([
    { "name": "health", "active": "false" },
    { "name": "business", "active": "false" },
    { "name": "entertainment", "active": "false" },
    { "name": "general", "active": "false" },
    { "name": "science", "active": "false" },
    { "name": "sports", "active": "false" },
    { "name": "technology", "active": "false" }
  ])
  const [mousePosition, setMousePosition] = useState("");

  const onCategoryClick = (categoryIndex) => {
    let cats = [];
    categories.map((cat) => {
      cat.active = "false";
      return cats.push(cat);
    })
    cats[categoryIndex].active = "true";
    setCategories(cats);
  }

  useEffect(() => {
    const mouseMove = document.addEventListener('mousemove', (e) => {

      if (e.y < window.innerHeight / 2) {
        setMousePosition('n-resize')
      } else if (e.y > window.innerHeight / 2) {
        setMousePosition('s-resize')
      }

    })
    return () => document.removeEventListener('mouseover', mouseMove);
  }, [])




  return (
    <Container cursor={mousePosition} >
      <ThemeProvider theme={theme}  >
        <>
          <GlobalStyle theme={theme} />
          <Body fetchError={fetchError} onClick={(e) => onNavigateArticle(mousePosition, e)} id="body">

            {fetchError.length !== 0 ? <Error />
              :
              <>
                <div>

                  {pageTitle.length > 0 ? <PageTitle>{pageTitle}</PageTitle> : <CategoryFilters>
                    {categories.map((cat, index) => {
                      return <Category key={cat.name} onClick={() => onCategoryClick(index)} theme={theme} active={cat.active} >{cat.name}</Category>
                    })}
                  </CategoryFilters>
                  }

                </div>

                <Article categories={categories} cursor={mousePosition} />
              </>
            }
            <SideMenu />
          </Body>

        </>
      </ThemeProvider>
    </Container>
  );
}

export default App;
